A,qBAAA,CAGA,qBAAA,CADA,eAAA,CADA,aAAA,CALA,aAAA,CACA,mBAAA,CAEA,oBAAA,CADA,UAMA,CACA,6CACA,SACA,CACA,wCAKA,gCAAA,CAJA,eAAA,CAKA,gBAAA,CAFA,eAAA,CAFA,SAAA,CACA,iBAIA,CACA,uCAIA,oBAAA,CAFA,aAAA,CACA,cAAA,CAFA,oBAAA,CAIA,wBAAA,CACA,qBAAA,CAEA,gBACA,CAKA,qJAEA,wBAAA,CACA,UACA,CACA,sCAEA,aAAA,CADA,oBAEA","sourcesContent":["<template>\r\n<on-click-outside :do=\"close\">\r\n    <div class=\"search-select\" :class=\"{ 'is-active': isOpen }\">\r\n        <button\r\n            ref=\"button\"\r\n            @click=\"open\"\r\n            type=\"button\"\r\n            class=\"search-select-input\"\r\n        >\r\n            <span v-if=\"value !== null\">{{ toTitleCase(value) }}</span>\r\n            <span v-else class=\"search-select-placeholder\">Address, MLS, Area</span>\r\n        </button>\r\n        <div ref=\"dropdown\" v-show=\"isOpen\" class=\"search-select-dropdown\">\r\n            <input\r\n                ref=\"search\"\r\n                class=\"search-select-search\"\r\n                name=\"omni\"\r\n                v-model=\"search\"\r\n                @keydown.esc=\"close\"\r\n                autocomplete=\"off\"\r\n\r\n            >\r\n            <ul ref=\"options\" v-show=\"filteredOptions.length > 0\" class=\"search-select-options\">\r\n                <li\r\n                    class=\"search-select-option\"\r\n                    v-for=\"option in filteredOptions\"\r\n                    :key=\"option.id\"\r\n                    @click=\"select(option.value)\"\r\n                >\r\n                {{ toTitleCase(option.value) }}\r\n                </li>\r\n            </ul>\r\n        <div\r\n            v-show=\"filteredOptions.length === 0\"\r\n            class=\"search-select-empty\">No results found for {{ search }}</div>\r\n        </div>\r\n    </div>\r\n</on-click-outside>\r\n</template>\r\n\r\n<script>\r\nimport OnClickOutside from './OnClickOutside.vue';\r\nexport default {\r\n    components: {\r\n        OnClickOutside\r\n    },\r\n    props: ['value', 'options', 'filterFunction','fieldValue'],\r\n    data() {\r\n        return {\r\n            isOpen: false,\r\n            search: '',\r\n        }\r\n    },\r\n    mounted(){\r\n        if(this.fieldValue !== '') {\r\n            this.select(this.fieldValue);\r\n        }\r\n    },\r\n    computed : {\r\n        filteredOptions() {\r\n            return this.filterFunction(this.search, this.options)\r\n        }\r\n    },\r\n    watch: {\r\n        search: function (newValue, oldValue) {\r\n            if (newValue.length > 2) {\r\n                this.filter(newValue);\r\n            }\r\n        }\r\n    },\r\n    methods: {\r\n        open() {\r\n            this.isOpen = true\r\n            this.$nextTick(() => {\r\n                this.$refs.search.focus()\r\n            })\r\n        },\r\n        close() {\r\n            if (! this.isOpen) return\r\n            this.isOpen = false\r\n            this.$refs.button.focus();\r\n        },\r\n        select(option) {\r\n            this.search = option\r\n            this.$emit('input', option)\r\n            this.close()\r\n        },\r\n        filter(search) {\r\n            this.$emit('input', search)\r\n        },\r\n        toTitleCase(str) {\r\n            return str.replace(/\\w\\S*/g, function(txt){\r\n                return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();\r\n            });\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.search-select {\r\n    position: relative;\r\n}\r\n.search-select-input {\r\n    -webkit-appearance: none;\r\n    -moz-appearance: none;\r\n    appearance: none;\r\n    text-align: left;\r\n    display: block;\r\n    width: 100%;\r\n    border-width: 1px;\r\n    padding: 0.38rem 0.7rem;\r\n    background-color: #fff;\r\n    cursor: pointer;\r\n    -webkit-user-select: none;\r\n    -moz-user-select: none;\r\n    -ms-user-select: none;\r\n    user-select: none;\r\n    border: 1px solid #ddd;\r\n}\r\n.search-select-input:focus {\r\n  outline: 0;\r\n  -webkit-box-shadow: none;\r\n  box-shadow: none;\r\n}\r\n.search-select-placeholder {\r\n  color: #8795a1;\r\n}\r\n.search-select.is-active .search-select-input {\r\n  -webkit-box-shadow: none;\r\n  box-shadow: none;\r\n}\r\n.search-select-dropdown {\r\n  position: absolute;\r\n  right: 0;\r\n  left: 0;\r\n  background-color: #fff;\r\n  padding: 0.5rem;\r\n  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);\r\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);\r\n  z-index: 50;\r\n  border: 1px solid #ddd;\r\n}\r\n.search-select-search {\r\n  display: block;\r\n  margin-bottom: 0.5rem;\r\n  width: 100%;\r\n  padding: 0.38rem 0.7rem;\r\n  background-color: #fff;\r\n  color: #2A2D2E;\r\n  border-radius: 0;\r\n  border: 1px solid #ddd;\r\n}\r\n.search-select-search:focus {\r\n  outline: 0;\r\n}\r\n.search-select-options {\r\n  list-style: none;\r\n  padding: 0;\r\n  position: relative;\r\n  overflow-y: auto;\r\n  -webkit-overflow-scrolling: touch;\r\n  max-height: 14rem;\r\n}\r\n.search-select-option {\r\n  padding: 0.5rem 0.75rem;\r\n  color: #2A2D2E;\r\n  cursor: pointer;\r\n  border-radius: 0.25rem;\r\n  -webkit-user-select: none;\r\n  -moz-user-select: none;\r\n  -ms-user-select: none;\r\n  user-select: none;\r\n}\r\n.search-select-option:hover {\r\n  background-color: #432021;\r\n  color: #fff;\r\n}\r\n.search-select-option.is-active,\r\n.search-select-option.is-active:hover {\r\n  background-color: #432021;\r\n  color: #fff;\r\n}\r\n.search-select-empty {\r\n  padding: 0.5rem 0.75rem;\r\n  color: #b8c2cc;\r\n}\r\n</style>\r\n\r\n"],"sourceRoot":""}]);
